import Section from "../../common/Section/index";
import Header from "../../common/Header/index";
import Container from "../../common/Container/index";
import { Image, ImageList } from "./styled";

function AuthorPage() {

  return (

    <Container>

      <Header
        title="O autorze"
      />

      <Section
        title="Micha Kowalczyk"
        body={
          <>
            <h3>
              Cze. Z wyksztacenia jestem architektem po studiach 5-letnich, natomiast postanowiem sprawdzi si w innej dziedzinie .
              Moje motto yciowe to:
            </h3>
            <h4 class="article__header article__header--boldItalic">"Po co gada, jeli wszystko wiadomo, a jeli nic
              nie wiadomo, to po co gada?"
            </h4>
            <p>W wolnym czasie lubi m.in. malowa. Poniej praca, ktr aktualnie tworz z anime "Demon Slayer"</p>
            <Image>
              <ImageList src="https://i.postimg.cc/1RqcPnqQ/Obraz.jpg" />
            </Image>
          </>
        }
      />

    </Container>
  );
}

export default AuthorPage;