import { StyledHeader } from "./styled";
import { ReactNode } from "react";

interface HeaderProps {
    title: ReactNode;
} // tworzymy interfejs, ktry bdzie mona powieli dla rnych komponentw

const Header = ({ title }: HeaderProps) => ( // okrelamy typ dla title
    <StyledHeader>
        <h1>{title}</h1>
    </StyledHeader>
)

export default Header;